import Vue from 'vue'
import Router from 'vue-router'
const _import = require('./_import_' + process.env.NODE_ENV)
Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'choseType',
      component: _import("choseType")
    }, {
      path: '/makeInfo',
      name: 'makeInfo',
      component: _import("makeInfo")
    },
    // {
    //   path: '/loveCn',
    //   name: 'loveCn',
    //   component: _import("loveCn")
    // },
    {
      path: '/wxDetail',
      name: 'wxDetail',
      component: _import("wxDetail")
    },
    {
      path: '/demo',
      name: 'demo',
      component: _import("demo")
    }, {
      path: '/mainIndex',
      name: 'mainIndex',
      component: _import("mainIndex/index")
    }
  ]
})



